const units = [
  'g',
  'dkg',
  'kg',
  'ml',
  'dl',
  'l',
  'te√°skan√°l',
  'ev≈ëkan√°l',
  'k√°v√©skan√°l',
  'mokk√°skan√°l',
  'db',
  'szem',
  'fej',
  'konzerv',
  'csomag',
];

interface Ingredient {
  name: string;
  amount: number;
  unit: string;
}

interface GetIngredients {
  ingredients: Ingredient[];
  incorrectIngredients: string[];
}

const rowRegExp = new RegExp(`^(\\d+\\.?\\d*) (${units.join('|')}) (.+)$`);

export function getIngredients(ingredientsCellValue: string): GetIngredients {
  const ingredientsList = ingredientsCellValue.split('\n');
  const result: GetIngredients = { ingredients: [], incorrectIngredients: [] };
  ingredientsList.forEach(row => {
    const matches = row.match(rowRegExp);
    if (matches && matches.length === 4)
      result.ingredients.push({
        amount: +matches[1],
        unit: matches[2],
        name: matches[3],
      } as Ingredient);
    else result.incorrectIngredients.push(row);
  });
  return result;
  // return ingredients;
}

// FIXME: Az al√°bbiakra nem vagyok b√ºszke.

function simplifyWeight(amount: number, unit: string, precision: number): string {
  if (unit === 'g') {
    if (10 <= amount) return simplifyWeight(amount / 10, 'dkg', precision);
  } else if (unit === 'dkg') {
    if (amount < 1) return simplifyWeight(amount * 10, 'g', precision);
    else if (100 <= amount) return simplifyWeight(amount / 100, 'kg', precision);
  } else if (unit === 'kg') {
    if (amount < 1) return simplifyWeight(amount * 100, 'dkg', precision);
  }
  return `${parseFloat(amount.toFixed(precision))} ${unit}`;
}

function simplifyVolume(amount: number, unit: string, precision: number): string {
  if (unit === 'ml') {
    if (10 <= amount) return simplifyWeight(amount / 100, 'dl', precision);
  } else if (unit === 'dl') {
    if (amount < 1) return simplifyWeight(amount * 100, 'ml', precision);
    else if (10 <= amount) return simplifyWeight(amount / 10, 'l', precision);
  } else if (unit === 'l') {
    if (amount < 1) return simplifyWeight(amount * 10, 'dl', precision);
  }
  return `${parseFloat(amount.toFixed(precision))} ${unit}`;
}

export function simplifyUnit(amount: number, unit: string, precision = 3): string {
  if (unit === 'g' || unit === 'dkg' || unit === 'kg') return simplifyWeight(amount, unit, precision);
  else if (unit === 'ml' || unit === 'dl' || unit === 'l') return simplifyVolume(amount, unit, precision);
  else return `${parseFloat(amount.toFixed(precision))} ${unit}`;
}
