import { AppConstants } from './initializeApp';
import fetchSpreadsheet from './fetchSpreadsheet';

function columnToLetter(column: number): string {
  let temp,
    letter = '';
  while (column > 0) {
    temp = (column - 1) % 26;
    letter = String.fromCharCode(temp + 65) + letter;
    column = (column - temp - 1) / 26;
  }
  return letter;
}

interface Recipe {
  name: string;
  price: number;
  energy: number;
  protein: number;
  carbohydrate: number;
  fat: number;
  water: number;
}

export interface EatingPlan {
  recipes: Recipe[];
  dataForDiagram: (string | number)[][];
  plan: {
    [day: string]: {
      meals: {
        [mealOfDay: string]: {
          name: string;
          isExist: boolean;
          energy: number;
          price: number;
        };
      };
    };
  };
}

const transpose = (a: string[][]): string[][] => a[0].map((_, c) => a.map(r => r[c]));

export async function getEatingPlan(parameters: AppConstants): Promise<EatingPlan> {
  return new Promise(async (resolve, reject) => {
    const FIRST_COLUMN_OF_PLAN = 8;
    const c: string[] = [];
    for (let i = 0; i <= parameters.numOfMenus; i++) c.push(columnToLetter(FIRST_COLUMN_OF_PLAN + i));
    const query = `select ${c.join(', ')} where ${columnToLetter(FIRST_COLUMN_OF_PLAN)} is not null`;
    const plan = await fetchSpreadsheet(query, parameters.spreadsheetId, parameters.tableIds.planSheet, 0);

    // const recipes = await fetchSpreadsheet(query, parameters.spreadsheetId, parameters.tableIds.planSheet, 0);
    const result: EatingPlan = { recipes: [], plan: {}, dataForDiagram: transpose(plan) };
    const recipeNamesInPlan: string[] = [];
    // console.log(transpose(plan));

    const days = plan[0];
    for (let i = 1; i < days.length; i++) {
      const day = days[i];
      // result[day] = [];
      result.plan[day] = { meals: {} };
      for (let j = 1; j < plan.length; j++) {
        const dish = plan[j][i];
        const mealOfDay = plan[j][0];
        if (dish) {
          result.plan[day].meals[mealOfDay] = { name: dish, isExist: false, energy: 0, price: 0 };
          recipeNamesInPlan.push(dish);
        }
      }
    }
    const recipeDataQuery = `select B, Q, R, S, T, U, V where (${recipeNamesInPlan
      .map(recipeName => `B = "${recipeName}"`)
      .join(' or ')})`;
    const recipeData = await fetchSpreadsheet(
      recipeDataQuery,
      parameters.spreadsheetId,
      parameters.tableIds.recipesSheet,
      1,
    );
    result.recipes = recipeData.map(d => {
      return { name: d[0], price: +d[1], energy: +d[2], carbohydrate: +d[3], fat: +d[4], water: +d[5] } as Recipe;
    });
    const recipeNames = result.recipes.map(r => r.name);
    Object.keys(result.plan).forEach(day => {
      Object.keys(result.plan[day].meals).forEach(mealOfDay => {
        const dishName = result.plan[day].meals[mealOfDay].name;
        const isExist = recipeNames.includes(dishName);
        result.plan[day].meals[mealOfDay].isExist = isExist;
        if (isExist) {
          result.plan[day].meals[mealOfDay].energy = result.recipes.filter(r => r.name === dishName)[0].energy;
          result.plan[day].meals[mealOfDay].price = result.recipes.filter(r => r.name === dishName)[0].price;
        }
      });
    });

    for (let i = 1; i < result.dataForDiagram.length; i++) {
      for (let j = 1; j < result.dataForDiagram[i].length; j++) {
        const dishName = result.dataForDiagram[i][j] + '';
        // if (recipeNames.includes(dishName)) {
        result.dataForDiagram[i][j] = recipeNames.includes(dishName)
          ? result.recipes.filter(r => r.name === dishName)[0].energy
          : 0;
        // }
      }
    }
    // console.log(recipeData);

    resolve(result);
  });
}
